'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import Image from 'next/image';
import { useRouter, useSearchParams } from 'next/navigation';
import { toast } from 'sonner';
import { useAuth } from '@/hooks/useAuth';
import { useCart } from '@/context/CartContext'; 
import { Banknote, CreditCard, Loader2, ArrowLeft, CheckCircle2 } from 'lucide-react';

import { Button } from '@/components/ui/button';
import { Card } from '@/components/ui/card';
import { Separator } from '@/components/ui/separator';
import {
    Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter
} from '@/components/ui/dialog';

import { PaymentMethod } from '@/types';

export default function PembayaranPage() {
    const router = useRouter();
    const searchParams = useSearchParams();
    const tableNumber = searchParams.get('table');
    const { user } = useAuth({ middleware: 'auth' });
    
    const { cart, totalPrice, totalItems, clearCart } = useCart();

    const [paymentMethods, setPaymentMethods] = useState<PaymentMethod[]>([]);
    const [selectedPayment, setSelectedPayment] = useState<number | null>(null);
    const [isProcessing, setIsProcessing] = useState(false);
    const [isSuccess, setIsSuccess] = useState(false);
    const [lastTrxCode, setLastTrxCode] = useState('');

    useEffect(() => {
        if (cart.length === 0) {
            toast.error("Keranjang kosong, kembali ke menu.");
            router.push('/pelanggan/produk');
        }
    }, [cart, router]);

    useEffect(() => {
        const fetchPayments = async () => {
            try {
                const res = await axios.get('/api/paymentMethods');
                setPaymentMethods(res.data.filter((p: any) => p.is_aktif));
            } catch (error) {
                console.error(error);
            }
        };
        fetchPayments();
    }, []);

    const formatRupiah = (num: number) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(num);

    const handlePayNow = async () => {
        if (!selectedPayment) return toast.error("Pilih metode pembayaran!");
        if (cart.length === 0) return toast.error("Keranjang kosong.");

        setIsProcessing(true);
        try {
            // Validate items
            const items = cart
                .filter(i => Number(i.qty) > 0)
                .map(item => ({
                    produk_resto_id: item.id,
                    jumlah: item.qty,
                    catatan: item.catatan || ''
                }));

            if (items.length === 0) throw new Error('No valid items to order');

            const payload: any = {
                payment_method_id: selectedPayment,
                total_bayar: Number(totalPrice) || 0,
                nama_pelanggan: user?.name ? `${user.name}${tableNumber ? ` (Meja ${tableNumber})` : ''}` : `Meja ${tableNumber || '-'} `,
                items
            };

            if (tableNumber) payload.nomor_meja = tableNumber;

            const res = await axios.post('/api/transaksiRestos', payload);

            setLastTrxCode(res.data.kode_transaksi);
            // Clear cart regardless of payment method
            clearCart();

            // If selected payment method is cash/tunai, redirect to success page
            const selectedMethod = paymentMethods.find(m => m.id === selectedPayment);
            const isCashMethod = selectedMethod?.nama_metode?.toLowerCase().includes('cash') || selectedMethod?.nama_metode?.toLowerCase().includes('tunai');

            if (isCashMethod) {
                router.push(`/pelanggan/pembayaran/sukses?code=${res.data.kode_transaksi}&type=cash`);
                return;
            }

            setIsSuccess(true);

        } catch (error: any) {
            console.error('Payment error:', error?.response?.data || error?.message || error);
            const serverMsg = error?.response?.data?.message || error?.message;
            toast.error(serverMsg || "Gagal memproses pesanan.");
        } finally {
            setIsProcessing(false);
        }
    };

    const handleSuccessClose = () => {
        router.push('/pelanggan/home'); 
    };

    return (
        <div className="min-h-screen bg-white font-sans text-gray-900 py-10 px-4 md:px-8">
            
            {/* Header */}
            <div className="max-w-6xl mx-auto mb-8 flex items-center">
                <Button variant="ghost" onClick={() => router.back()} className="mr-4 text-gray-500">
                    <ArrowLeft className="w-5 h-5" />
                </Button>
                <h1 className="text-3xl font-extrabold text-[#D93F21] mx-auto pr-10">Payments Order</h1>
            </div>

            <div className="max-w-6xl mx-auto bg-white rounded-[2.5rem] shadow-xl overflow-hidden border border-gray-100">
                <div className="flex flex-col lg:flex-row min-h-[600px]">
                    
                    {/* KIRI: IMAGE (Sesuai Gambar) */}
                    <div className="w-full lg:w-1/2 relative min-h-[300px] lg:min-h-full">
                        <Image 
                            src="/images/gambar-pembayaran.png" 
                            alt="Payment Illustration"
                            fill
                            className="object-cover"
                        />
                        <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent flex items-end p-10">
                            <div className="text-white">
                                <h3 className="text-2xl font-bold mb-2">Selesaikan Pesananmu</h3>
                                <p className="text-sm opacity-90">Nikmati hidangan lezat segera setelah pembayaran dikonfirmasi.</p>
                            </div>
                        </div>
                    </div>

                    {/* KANAN: FORM PEMBAYARAN */}
                    <div className="w-full lg:w-1/2 p-8 md:p-12 flex flex-col justify-center">
                        {/* 1. Customer Info */}
                        <div className="mb-8">
                            <h3 className="text-[#D93F21] font-bold text-lg mb-4">Customer Information</h3>
                            <div className="space-y-3 pl-1">
                                <div>
                                    <p className="text-xs text-gray-400 font-medium uppercase tracking-wide">Customer Name</p>
                                    <p className="font-bold text-gray-900">{user?.name}</p>
                                </div>
                                <div>
                                    <p className="text-xs text-gray-400 font-medium uppercase tracking-wide">Table Number</p>
                                    <p className="font-bold text-gray-900 text-xl">{tableNumber || '-'}</p>
                                </div>
                            </div>
                        </div>

                        {/* 2. Payment Summary */}
                        <div className="mb-8">
                            <h3 className="text-[#D93F21] font-bold text-lg mb-4">Payment Summary</h3>
                            <div className="space-y-2 text-sm pl-1">
                                <div className="flex justify-between text-gray-500">
                                    <span>Subtotal ({totalItems} items)</span>
                                    <span className="font-medium text-gray-900">{formatRupiah(totalPrice)}</span>
                                </div>
                                <div className="flex justify-between text-gray-500">
                                    <span>Discount sales</span>
                                    <span className="font-medium text-gray-900">Rp 0</span>
                                </div>
                                <div className="flex justify-between text-gray-500">
                                    <span>Total tax</span>
                                    <span className="font-medium text-gray-900">Rp 0</span>
                                </div>
                                <Separator className="my-3" />
                                <div className="flex justify-between font-bold text-lg text-gray-900">
                                    <span>Total</span>
                                    <span>{formatRupiah(totalPrice)}</span>
                                </div>
                            </div>
                        </div>

                        {/* 3. Payments Method */}
                        <div className="mb-10">
                            <h3 className="text-[#D93F21] font-bold text-lg mb-4">Payments</h3>
                            <div className="grid grid-cols-2 gap-4">
                                {paymentMethods.map((method) => (
                                    <div 
                                        key={method.id}
                                        onClick={() => setSelectedPayment(method.id)}
                                        className={`
                                            cursor-pointer rounded-xl border-2 p-4 flex flex-col items-center justify-center gap-2 transition-all duration-200
                                            ${selectedPayment === method.id 
                                                ? 'border-[#D93F21] bg-red-50 text-[#D93F21]' 
                                                : 'border-gray-100 hover:border-gray-300 text-gray-600'
                                            }
                                        `}
                                    >
                                        {method.nama_metode.toLowerCase().includes('cash') || method.nama_metode.toLowerCase().includes('tunai') ? (
                                            <Banknote className="w-8 h-8" />
                                        ) : (
                                            <CreditCard className="w-8 h-8" />
                                        )}
                                        <span className="font-bold text-sm">{method.nama_metode}</span>
                                    </div>
                                ))}
                            </div>
                        </div>

                        {/* Button */}
                        <Button 
                            className="w-full bg-[#D93F21] hover:bg-[#b9351b] text-white font-bold py-7 rounded-2xl text-lg shadow-xl shadow-orange-200 transition-transform active:scale-95"
                            onClick={handlePayNow}
                            disabled={isProcessing}
                        >
                            {isProcessing ? <Loader2 className="animate-spin mr-2" /> : "Pay Now"}
                        </Button>
                    </div>
                </div>
            </div>

            {/* Modal Sukses */}
            <Dialog open={isSuccess} onOpenChange={setIsSuccess}>
                <DialogContent className="sm:max-w-sm text-center p-8">
                    <DialogHeader>
                        <div className="mx-auto w-16 h-16 bg-green-100 rounded-full flex items-center justify-center mb-4 text-green-600">
                            <CheckCircle2 className="w-10 h-10" />
                        </div>
                        <DialogTitle className="text-center text-2xl font-bold">Pesanan Berhasil!</DialogTitle>
                        <DialogDescription className="text-center text-gray-500 mt-2">
                            Kode Pesanan: <span className="font-mono font-bold text-gray-900">{lastTrxCode}</span>
                            <br/>
                            Pesanan Anda akan segera disiapkan. Silakan lakukan pembayaran di kasir jika memilih Tunai.
                        </DialogDescription>
                    </DialogHeader>
                    <DialogFooter className="mt-6">
                        <Button onClick={handleSuccessClose} className="w-full bg-[#D93F21] hover:bg-[#b9351b] rounded-xl py-6">
                            Kembali ke Dashboard
                        </Button>
                    </DialogFooter>
                </DialogContent>
            </Dialog>

        </div>
    );
}